import { action, makeAutoObservable, observable } from "mobx";

import { createContext } from "react";


class SegregationStore {
    @observable segregatioProducts:any = [] //   
    @observable currentCategory: any = [] //    
    @observable currentProduct: any = [] //     
    @observable isLoading:boolean = true //     
    @observable isFilters:boolean = false //     catalogLayout
    @observable isNotProduct: boolean = false //        
    @observable brands: string = '' //   brands
    @observable genders: string = '' //   genders
    @observable notes: string = '' //   notes
    @observable price: number = 500

        constructor(){
            makeAutoObservable(this) 
        }

      // 
         @action priceDescending = async (sortMethod: string) => {
          
        //       sortMethod
        switch (sortMethod) {
    
          case '  ':
           await fetch( 
              'https://64e6020b09e64530d17f6dd0.mockapi.io/Flavors?' //   
            )
              .then((res) => res.json()) //     json
              .then((data) => { //   json
                this.segregatioProducts = data; //    
              });
           
              this.segregatioProducts = this.segregatioProducts.sort((a:any, b:any) => a.price - b.price); //         
              this.currentProduct = this.segregatioProducts //   
            break;
    
          case '  ':
            await fetch(
              'https://64e6020b09e64530d17f6dd0.mockapi.io/Flavors?' //   
            )
              .then((res) => res.json()) //     json
              .then((data) => { //   json
                this.segregatioProducts = data; //      
              });

            this.currentProduct = this.segregatioProducts.sort((a:any, b:any) => b.price - a.price); //         
            this.segregatioProducts = [...this.currentProduct]; //            
            break;
    
            case '  ':
              await fetch(
                'https://64e6020b09e64530d17f6dd0.mockapi.io/Flavors?'
              )
                .then((res) => res.json()) //   
                .then((data) => { //     json
                  this.segregatioProducts = data; //      
                });
              this.currentProduct = this.segregatioProducts.sort((a:any, b:any) => b.id - a.id); //        
              this.segregatioProducts = [...this.currentProduct]; //            
            break;
    
            case ' ':
              await fetch(
                'https://64e6020b09e64530d17f6dd0.mockapi.io/Flavors?'
              )
                .then((res) => res.json()) //   
                .then((data) => { //     json
                  this.segregatioProducts = data; //      
                });
                this.currentProduct = this.segregatioProducts.sort((a:any, b:any) => a.title.length - b.title.length); //        title
                this.segregatioProducts = [...this.currentProduct]; //            
          
            break;
    
            case ' ':
              await fetch(
                'https://64e6020b09e64530d17f6dd0.mockapi.io/Flavors?'
              )
                .then((res) => res.json()) //   
                .then((data) => { //     json
                  this.segregatioProducts = data; //      
                });
              this.currentProduct = this.segregatioProducts.sort((a:any, b:any) => a.id - b.id); //   
              this.segregatioProducts = [...this.currentProduct]; //            
            break;
        }
          //       sortMethod
        };
      // 

      // 
        @action setFilters = (filters:boolean) =>{
            this.isFilters = filters;
      
            if(this.isFilters){
                fetch(
                    'https://64e6020b09e64530d17f6dd0.mockapi.io/Flavors?'
                  )
                    .then((res) => res.json())
                    .then((data) => {
                      this.segregatioProducts = data;      
                      this.currentProduct = [...this.segregatioProducts]
                    });
            }else{
                this.segregatioProducts = []            
            }
               
        }
        
        @action getBrands = (brand:string) =>{
            this.brands = brand //      
            if(this.segregatioProducts.length !== 0){ //         
                this.currentProduct = [...this.segregatioProducts].filter((product:any) => product.brand === this.brands) //    
            }else{
                this.segregatioProducts = [] //       
            }
            if(this.genders !== ''){
                this.currentProduct = [...this.currentProduct].filter((product:any) => product.type === this.genders)
            }
            if(this.notes !== ''){
              this.currentProduct = [...this.currentProduct].filter((product:any) => product.note === this.notes)
            }
        } 
    
        @action getGender = (gender:string) =>{
         this.genders = gender //      
          if(this.brands === ''){
            this.currentProduct = this.segregatioProducts.filter((product:any) => product.type === this.genders)
          }else{
            this.currentCategory = [...this.currentProduct].filter((product:any) => product.type === this.genders)
            this.currentProduct = [...this.currentCategory]
             if(this.currentProduct.length === 0){
                this.currentProduct = [...this.segregatioProducts]
                .filter((product:any) => product.brand === this.brands) //    
                .filter((product:any) => product.type === this.genders) //    
             }
          }
          if(this.notes !== ''){
            this.currentProduct = [...this.currentProduct].filter((product:any) => product.type === this.genders)
          }
        
          if(this.brands !== '' && this.notes !== ''){
            this.currentCategory = [...this.currentProduct].filter((product:any) => product.type === this.genders)
            this.currentProduct = [...this.currentCategory]
          }
          if(this.brands !== '' && this.notes !== '' && this.price !== 500){
            this.currentCategory = [...this.currentProduct].filter((product:any) => product.type === this.genders)
            this.currentProduct = [...this.currentCategory]
          }
        }
        @action getNotes = (notes:string) =>{
            this.notes = notes //      

            if(this.segregatioProducts.length !== 0){ //         
              this.currentProduct = [...this.segregatioProducts].filter((product:any) => product.note === this.notes) //    
           
            }else{
              this.segregatioProducts = [] //       
            }
            if(this.brands !== ''){
              this.currentProduct = [...this.currentProduct].filter((product:any) => product.brand === this.brands)
            }
            if(this.genders !== ''){
              this.currentProduct = [...this.currentProduct].filter((product:any) => product.type === this.genders)
            }
            if(this.genders !== '' && this.brands !== ''){
              this.currentProduct = [...this.currentProduct].filter((product:any) => product.type === this.genders)
            }
            if(this.genders !== '' && this.brands !== '' && this.price !== 500){
              this.currentProduct = [...this.currentProduct].filter((product:any) => product.type === this.genders)
            }
        }

        @action getPrice = (price: number) => {
            this.price = price;
            if(this.segregatioProducts.length !== 0){
              this.currentProduct = [...this.segregatioProducts].sort((a:any, b:any) => a.price - b.price)
              .filter((product:any) => product.price > +this.price)
            }else{
              this.segregatioProducts = [] //       
            }
            if(this.brands !== ''){
              this.currentProduct = [...this.currentProduct].filter((product:any) => product.brand === this.brands)
            }
            if(this.genders !== ''){
              this.currentProduct = [...this.currentProduct].filter((product:any) => product.type === this.genders)
            }
            if(this.notes !== ''){
              this.currentProduct = [...this.currentProduct].filter((product:any) => product.note === this.notes)
            }
            if(this.brands !== '' && this.notes !== ''){
              this.currentProduct = [...this.currentProduct].filter((product:any) => product.price === this.price)
            }
            if(this.genders !== '' && this.brands !== ''){
              this.currentProduct = [...this.currentProduct].filter((product:any) => product.price === this.price)
            }
        }
      // 
   
}
//   
export default createContext(new SegregationStore()) 
