import { action, makeAutoObservable, observable } from "mobx";
import { createContext } from "react";


class BasketStore {

    @observable cardsData = JSON.parse(localStorage.getItem("basketProduct") || '[]')  
    
    constructor(){
        makeAutoObservable(this)
    }
    @action getCardsData(card:any){
        this.cardsData = card;
    }    
    
    @action deleteCard(id:string){ //    
      const findObject = this.cardsData.find((obj:{ volume:number, id:string, price:number })=> obj.id === id) //   
      this.cardsData.splice(this.cardsData.indexOf(findObject), 1) //   
   
    if(findObject){
        localStorage.removeItem(findObject.id) //   id 
        localStorage.setItem("basketProduct", JSON.stringify(this.cardsData)) //   
    }
    }

    @action decrease(id:string){ //    
        const priceCardsData = JSON.parse(localStorage.getItem("basketProduct") || '[]')  //  price
        const findObject = this.cardsData.find((obj:{ volume:number, id:string, price:number })=> obj.id === id) //   
        findObject.volume--; //  
        if(findObject.volume <= 0){ //   0    1
            findObject.volume = 1
        }

        const priceFind = priceCardsData.find((obj:{ volume:number, id:string, price:number })=> obj.id === id) //  price
        const newNum = Number.parseInt(findObject.price) - Number.parseInt(priceFind.price);  //  
        findObject.price = newNum //   

        if(findObject.price <= 0){  //  <=  
            findObject.price = Number.parseInt(priceFind.price) //    price  price
        }
        
    }
    @action increase(id:string){ //    

        const priceCardsData = JSON.parse(localStorage.getItem("basketProduct") || '[]') //  price

        const findObject = this.cardsData.find((obj:{ volume:number, id:string, price:number })=> obj.id === id) //   

        const priceFind = priceCardsData.find((obj:{ volume:number, id:string, price:number })=> obj.id === id) //  price
        const newNum = Number.parseInt(findObject.price) + Number.parseInt(priceFind.price);  //   
        findObject.price = newNum //   
        findObject.volume++; //  
        if(findObject.volume >= 100){ //   100    100          100
            findObject.volume = 100
            findObject.price = Number.parseInt(priceFind.price) * 100 
        }
    }   
}

export default createContext(new BasketStore()) 

